<template>
  <div>
    <div class="tab">
      <nav>
        <span
          v-for="(item, key) in tabMenu"
          :key="key"
          :class="activeName === key && 'active'"
          @click="tabClick(key)">
          {{item.name}}
        </span>
      </nav>
    </div>
    <section class="container" v-if="activeName === 0">
      <div class="content">
        <h1 class="tab-title">个人资料</h1>
        <el-row :gutter="20" class="row-box">
          <el-col :span="3">
            <div class="row-title">头像</div>
          </el-col>
          <el-col :span="21">
            <el-upload
              class="avatar-uploader"
              action="http://up-z2.qiniu.com"
              :show-file-list="false"
              :on-success="uploadSuccess"
              :on-error="uploadError"
              :data="{token}">
              <div class="upload-cover" v-if="formData.cover">
                <img :src="formData.cover" alt="cover">
              </div>
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-col>
        </el-row>
        <el-row :gutter="20" class="row-box">
          <el-col :span="3">
            <div class="row-title">用户昵称</div>
          </el-col>
          <el-col :span="21">
            <el-input class="input-common" placeholder="填写你的用户昵称" v-model="formData.nickname" suffix-icon="el-icon-edit"></el-input>
          </el-col>
        </el-row>
        <el-row :gutter="20" class="row-box">
          <el-col :span="3">
            <div class="row-title">个人介绍</div>
          </el-col>
          <el-col :span="21">
            <el-input class="input-common" placeholder="填写职业技能、擅长的事情、喜欢的事情等" v-model="formData.introduction" suffix-icon="el-icon-edit"></el-input>
          </el-col>
        </el-row>
        <el-row :gutter="20" class="row-box">
          <el-col :span="3">
            <div class="row-title">个人主页</div>
          </el-col>
          <el-col :span="21">
            <el-input class="input-common" placeholder="填写你的个人主页" v-model="formData.website" suffix-icon="el-icon-edit"></el-input>
          </el-col>
        </el-row>
        <div class="form-btn">
          <el-button type="primary" @click="updateUserInfo">保存</el-button>
        </div>
      </div>
    </section>
    <section class="container" v-else-if="activeName === 1">
      <div class="content">
        <h1 class="tab-title">积分奖励记录</h1>
        <div class="table-box">
          <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column
              prop="id"
              label="ID"
              width="180">
            </el-table-column>
            <el-table-column
              prop="integralAdd"
              label="奖励积分"
              width="180">
            </el-table-column>
            <el-table-column
              prop="channelId"
              label="积分来源">
              <template slot-scope="scope">
                {{scope.row.channelId | integralSource}}
              </template>
            </el-table-column>
            <el-table-column
              prop="createdAt"
              label="时间">
              <template slot-scope="scope">
                <span>{{scope.row.createdAt | dateFormat('YYYY-MM-DD HH:mm:ss')}}</span>
              </template>
            </el-table-column>
          </el-table>
          <div class="table-page">
            <el-pagination
              background
              layout="prev, pager, next"
              :total="tableData.length">
            </el-pagination>
          </div>
        </div>
      </div>
    </section>
    <section class="container" v-else-if="activeName === 2">
      <div class="content">
        <h1 class="tab-title">修改密码</h1>

      </div>
    </section>
  </div>
</template>

<script>
  import getUploadToken from '../../libs/upload-token';
  import {mapState, mapActions} from 'vuex';
  export default {
    data() {
      return {
        tabMenu: [
          {name: '个人资料'},
          {name: '积分记录'},
          {name: '修改密码'},
        ],
        activeName: 0,
        formData: {
          cover: '',
          nickname: '',
          website: '',
          introduction: ''
        },
        token: '',
        tableData: []
      };
    },
    created() {
      document.title = '个人中心';
      this.getImageToken();
      this.formData = this.userInfo;
    },
    computed: {
      ...mapState({
        // 获取当前登录用户相关信息
        userInfo: state => state.user.userInfo
      })
    },
    methods: {
      ...mapActions({
        updateUser: 'user/updateUser',
        getIntegral: 'integral/getIntegral'
      }),
      // 获取用户积分流水
      async getIntegralList() {
        const param = {
          userId: this.userInfo.id,
          page: 1,
          pageSize: 10,
        };
        let req = await this.getIntegral(param);
        if (req.data.code === 200) {
          this.tableData = req.data.data.data;
        }
      },
      // 更新用户资料
      async updateUserInfo() {
        await this.updateUser({...this.formData});
        this.$message({message: '资料更新成功！', type: 'success'});
      },
      // 选项卡切换
      tabClick(type) {
        this.activeName = type;
        if (type === 1) {
          this.getIntegralList();
        }
      },
      // 获取七牛云上传 token
      async getImageToken() {
        try {
          const res = await getUploadToken();
          this.token = res.token;
        } catch (e) {
          console.log(e)
        }
      },
      // 上传图片成功
      uploadSuccess(response) {
        this.formData.cover = `http://image.burongyi.com/${response.key}`;
        this.$message({message: '上传成功！', type: 'success'});
        console.log(this.formData);
      },
      // 上传图片失败
      uploadError(response) {
        this.$message({message: '上传失败！', type: 'success'});
        console.log(response);
      },
    }
  }
</script>

<style scoped lang="scss">
  .tab {
    background-color: #fff;
    height: 65px;
    line-height: 65px;
    box-shadow: 0.053333rem 0.053333rem 0.053333rem rgba(0, 0, 0, 0.03);
    nav {
      width: 70%;
      margin: 0 auto;
    }
    span {
      margin-right: 40px;
      font-size: 20px;
      color: #71777c;
      cursor: pointer;
      &.active {
        color: #409EFF;
      }
    }
  }

  .container {
    box-sizing: border-box;
    position: relative;
    width: 70%;
    margin: 24px auto;

    .content {
      box-sizing: border-box;
      padding: 70px 60px;
      position: relative;
      width: 100%;
      background: #fff;
      border-radius: 8px;
    }
  }

  .tab-title {
    font-size: 2em;
    padding-bottom: 20px;
    border-bottom: 1px solid #f1f1f1;
  }

  .row-box {
    display: flex;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid #f1f1f1;
    min-height: 100px;
    .row-title {
      font-size: 20px;
    }
  }

  .form-btn {
    padding: 60px 0 0;
    text-align: center;
    button {
      width: 200px;
    }
  }

  .table-page {
    text-align: right;
    margin-top: 40px;
  }
</style>



