const qiniu = require('qiniu');

// "www.qiniu.com" 七牛空间 密钥
const ACCESS_KEY = "JLidToswpAgFU3Q-q0UPwCNAOYYKqvYqZni5D_yd";
const SECRET_KEY = "v0_8fLi5T7o0B-ml62n0FNC9QAksg4oKTZ4HrtNJ";

export default async function getUploadToken() {
  return new Promise((resolve, reject) => {
    let mac = new qiniu.auth.digest.Mac(ACCESS_KEY, SECRET_KEY);
    let options = {
      scope: "dave",
      expires: 7200
    };
    let putPolicy = new qiniu.rs.PutPolicy(options);
    let uploadToken = putPolicy.uploadToken(mac);

    if (uploadToken) {
      resolve({
        token: uploadToken
      })
    } else {
      reject()
    }
  })
}




